import React from "react";
import ProjectStatusSelector from "../components/ProjectStatusSelector";
import totalSteps from "../utils/totalSteps";

const ProjectItems = ({ project, orders }) => {
  const projectStatusItems = (number, order) => {
    if (!order.steps || order.steps.length < number) {
      order.steps = [];
      for (let i = 0; i < number; i++) {
        order.steps.push("none");
      }
    }

    const html = [];
    order.steps.forEach((step, i) => {
      html.push(
        <div
          id={"step" + i}
          key={"step" + i}
          className="step-status"
          style={{
            width: (document.documentElement.clientWidth - 500) / number + "px",
          }}>
          <ProjectStatusSelector stepStatus={step} index={i} order={order} />
        </div>
      );
    });

    return html;
  };

  return (
    <>
      {orders.map((item) => {
        return (
          <div className="project-item" key={item.id} id={item.id}>
            <div style={{ width: "300px", paddingRight: "10px" }}>
              {item.title}
            </div>
            {projectStatusItems(totalSteps(project.stages), item)}
          </div>
        );
      })}
    </>
  );
};

export default ProjectItems;
