const number = 15;

if (!localStorage.getItem("cmrLastUserId")) {
  localStorage.setItem("cmrLastUserId", JSON.stringify(number));
}
export const getUserIndex = () => {
  return JSON.parse(localStorage.getItem("cmrLastUserId"));
};
export const setUserIndex = (newNumber) => {
  localStorage.setItem("cmrLastUserId", JSON.stringify(newNumber));
};
