import React, { useEffect, useState } from "react";
import API from "../data";
import EventInfo from "../components/EventInfo";
import PreviewWindow from "../components/PreviewWindow";
import Loader from "./Loader";


const Calendar = () => {
  const todayIs = new Date();
  const [monthNumber, setMonthNumber] = useState(todayIs.getMonth());
  const [yearNumber, setYearNumber] = useState(todayIs.getFullYear());
  const monthNames = [
    "Январь",
    "Февраль",
    "Март",
    "Апрель",
    "Май",
    "Июнь",
    "Июль",
    "Август",
    "Сентябрь",
    "Октябрь",
    "Ноябрь",
    "Декабрь",
  ];
  let [eventId, setEventId] = useState();
  let [PreviewWindowStatus, setPreviewWindowStatus] = useState(false);
  const date = new Date(yearNumber, monthNumber, 1);
  const monthStart = date.getDay();
  const [orders, setOrders] = useState();
  const [events, setEvents] = useState();

  useEffect(() => {
    API.orders.fetchAll().then((data) => setOrders(data));
  }, []);
  useEffect(() => {
    API.events.fetchAll().then((data) => setEvents(data));
  }, []);

	if (events && orders) {
    const orderEvents = orders.filter((item) => item.eventDate !== "");
    const allEvents = [...events, ...orderEvents];

		const thisMonthEvents = allEvents.filter((item) => {
      const itemDate = new Date(item.eventDate);
      return (
        itemDate.getMonth() === monthNumber &&
        itemDate.getFullYear() === yearNumber
      );
    });

    function thisDayEvents(number) {
      return thisMonthEvents.filter((item) => {
        const itemDate = new Date(item.eventDate);
        return itemDate.getDate() === number;
      });
    }

    const daysWithEvents = [];
    thisMonthEvents.forEach((item) => {
      daysWithEvents.push(new Date(item.eventDate).getDate());
    });

    function upMonth() {
      setMonthNumber(monthNumber + 1);
      setMonthNumber((prevState) =>
        prevState === 12 ? (prevState = 0) : prevState
      );
      setYearNumber((prevState) =>
        monthNumber === 11 ? prevState + 1 : prevState
      );
    }

    function downMonth() {
      setMonthNumber(monthNumber - 1);
      setMonthNumber((prevState) =>
        prevState === -1 ? (prevState = 11) : prevState
      );
      setYearNumber((prevState) =>
        monthNumber === 0 ? prevState - 1 : prevState
      );
    }

    const handleEventId = (id) => {
      setEventId(id);
      setPreviewWindowStatus(true);
    };

    const CalendarBody = () => {
      const html = [];
      if (monthStart === 0) {
        for (let i = 0; i < 6; i++) {
          html.push(<div className="item white" key={"e" + i}></div>);
        }
      }
      if (
        monthStart === 2 ||
        monthStart === 3 ||
        monthStart === 4 ||
        monthStart === 5 ||
        monthStart === 6
      ) {
        for (let i = 0; i < monthStart - 1; i++) {
          html.push(<div className="item white" key={"e" + i}></div>);
        }
      }

      if (
        monthNumber === 0 ||
        monthNumber === 2 ||
        monthNumber === 4 ||
        monthNumber === 6 ||
        monthNumber === 7 ||
        monthNumber === 9 ||
        monthNumber === 11
      ) {
        for (let i = 1; i <= 31; i++) {
          daysWithEvents.includes(i)
            ? html.push(
                <div className="item" key={"d" + i}>
                  <div className="day-number">{i}</div>
                  {thisDayEvents(i).map((event) => {
                    return (
                      <div
                        className={"event-container " + event.eventType}
                        key={"event" + event.id}
                        onClick={() => handleEventId(event.id)}>
                        {event.title}
                      </div>
                    );
                  })}
                </div>
              )
            :
          html.push(
            <div className="item" key={"d" + i}>
              <div className="day-number">{i}</div>
            </div>
          );
        }
      }
      if (monthNumber === 1 && yearNumber % 4 === 0) {
        for (let i = 1; i <= 29; i++) {
          daysWithEvents.includes(i)
            ? html.push(
                <div className="item" key={"d" + i}>
                  <div className="day-number">{i}</div>
                  {thisDayEvents(i).map((event) => {
                    return (
                      <div
                        className={"event-container " + event.eventType}
                        key={"event" + event.id}
                        onClick={() => handleEventId(event.id)}>
                        {event.title}
                      </div>
                    );
                  })}
                </div>
              )
            :
          html.push(
            <div className="item" key={"d" + i}>
              <div className="day-number">{i}</div>
            </div>
          );
        }
      }
      if (monthNumber === 1 && yearNumber % 4 !== 0) {
        for (let i = 1; i <= 28; i++) {
          daysWithEvents.includes(i)
            ? html.push(
                <div className="item" key={"d" + i}>
                  <div className="day-number">{i}</div>
                  {thisDayEvents(i).map((event) => {
                    return (
                      <div
                        className={"event-container " + event.eventType}
                        key={"event" + event.id}
                        onClick={() => handleEventId(event.id)}>
                        {event.title}
                      </div>
                    );
                  })}
                </div>
              )
            :
          html.push(
            <div className="item" key={"d" + i}>
              <div className="day-number">{i}</div>
            </div>
          );
        }
      }
      if (
        monthNumber === 3 ||
        monthNumber === 5 ||
        monthNumber === 8 ||
        monthNumber === 10
      ) {
        for (let i = 1; i <= 30; i++) {
          daysWithEvents.includes(i)
            ? html.push(
                <div className="item" key={"d" + i}>
                  <div className="day-number">{i}</div>
                  {thisDayEvents(i).map((event) => {
                    return (
                      <div
                        className={"event-container " + event.eventType}
                        key={"event" + event.id}
                        onClick={() => handleEventId(event.id)}>
                        {event.title}
                      </div>
                    );
                  })}
                  <div>test</div>
                </div>
              )
            :
          html.push(
            <div className="item" key={"d" + i}>
              <div className="day-number">{i}</div>
            </div>
          );
        }
      }
      return html;
    };

    return (
      <div className="month-container">
        <div className="button-container">
          <div className="month-title">
            <h3>
              {monthNames[monthNumber]} {yearNumber}
            </h3>
          </div>
          <button id="prev-month" onClick={downMonth}>
            <i className="bi bi-arrow-left-square"></i>
          </button>
          <button id="next-month" onClick={upMonth}>
            <i className="bi bi-arrow-right-square"></i>
          </button>
        </div>
        <CalendarBody />
        <PreviewWindow
          status={PreviewWindowStatus}
          setStatus={setPreviewWindowStatus}>
          <EventInfo items={allEvents} id={eventId} />
        </PreviewWindow>
      </div>
    );
  }

  return <Loader />;
};

export default Calendar;
