import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import API from "../data";
import Box from "../components/ui/Box";
import { Divider } from "@mui/material";
import ClientNotes from "../components/NotesField";
import ClientInfoContainer from "../components/ClientInfoContainer";
import {
  Button,
  Dialog,
  DialogActions,
  DialogTitle,
  DialogContent,
  DialogContentText,
} from "@mui/material";
import ArrowBackIosTwoToneIcon from "@mui/icons-material/ArrowBackIosTwoTone";
import DeleteForeverTwoToneIcon from "@mui/icons-material/DeleteForeverTwoTone";
import EditTwoToneIcon from "@mui/icons-material/EditTwoTone";
import Loader from "../components/Loader";

const Order = () => {
  const history = useNavigate();
  const params = useParams();
  const id = params.id;
  const [currentOrder, setCurrentOrder] = useState();
  const [open, setOpen] = useState(false);
  const [clients, setClients] = useState();
  const [newDataInput, setNewDataInput] = useState();

  useEffect(() => {
    API.orders.find(id).then((item) => setCurrentOrder(item));
    API.clients.fetchAll().then((data) => setClients(data));
  }, []);

  const handleChangeInput = ({ target }) => {
    setNewDataInput(target.value);
  };
  const handleAddNote = () => {
    API.orders.update(id, { notes: newDataInput });
    setCurrentOrder((prevState) => ({
      ...prevState,
      notes: newDataInput,
    }));
  };

  useEffect(() => {
    if (currentOrder) {
      setNewDataInput(currentOrder.notes);
    }
  }, [currentOrder]);

  const handleDelete = () => {
    API.orders.deleteItem(id).then(history("/orders"));
  };
  const handleChange = () => {
    history(`/order/${id}/edit`);
  };
  const handleBack = () => {
    history("/orders");
  };

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  if (currentOrder && clients) {
    const client = clients.find(
      (item) => Number(item.id) === Number(currentOrder.clientId)
    );

    return (
      <div className="content-container">
        <h1>
          Заказ #{currentOrder.id} от {currentOrder.date}
        </h1>
        <div className="order-status">Статус: {currentOrder.status}</div>
        <div
          style={{
            display: "flex",
            flexWrap: "wrap",
            width: "100%",
            justifyContent: "center",
          }}>
          <div className="slim-container">
            <Box
              style={{
                flexDirection: "column",
                justifyContent: "flex-start",
              }}>
              <Button
                variant="contained"
                onClick={handleBack}
                startIcon={<ArrowBackIosTwoToneIcon />}
                disableElevation>
                Назад
              </Button>
              <Button
                variant="outlined"
                onClick={handleOpen}
                startIcon={<DeleteForeverTwoToneIcon />}>
                Удалить
              </Button>
              <Button
                variant="outlined"
                onClick={handleChange}
                startIcon={<EditTwoToneIcon />}>
                Редактировать
              </Button>
            </Box>
            <ClientInfoContainer client={client} />
          </div>

          <div className="bold-container">
            <Box>
              <div
                style={{
                  fontWeight: "bold",
                  fontSize: "24px",
                  margin: "10px 0",
                }}>
                {currentOrder.title}
              </div>
              <div style={{ margin: "10px 0" }}>
                <b>Дата:</b> {currentOrder.eventDate}
              </div>
              <div style={{ margin: "10px 0" }}>
                <b>Время:</b> {currentOrder.time}
              </div>
              <div style={{ margin: "10px 0" }}>
                <b>Адрес:</b> {currentOrder.place}
              </div>
              <Divider />
              <div
                style={{
                  fontWeight: "bold",
                  fontSize: "20px",
                  margin: "10px 0",
                }}>
                Стоимость: {currentOrder.cost} руб.
              </div>
            </Box>
            <ClientNotes
              handleAddNote={handleAddNote}
              handleChangeInput={handleChangeInput}
              newDataInput={newDataInput}
              notes={currentOrder.notes}
            />
          </div>
        </div>
        <Dialog
          open={open}
          keepMounted
          onClose={handleClose}
          aria-describedby="alert-dialog-slide-description">
          <DialogTitle>{"Внимание!"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              Это действие необратимо. Вы точно хотите удалить заказ?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Отмена</Button>
            <Button onClick={handleDelete}>Удалить</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
  return <Loader />;
};

export default Order;
