import React from "react";
const TableHeader = ({ column }) => {
  return (
    <thead>
      <tr style={{height: "50px", borderBottom: "1px solid rgba(0,0,0, 0.03"}}>
        {column.map((item, index) => {
          return <th key={"col-" + index} style={{padding: "5px 10px", minWidth: "40px"}}>{item.name}</th>;
        })}
      </tr>
    </thead>
  );
};

export default TableHeader;
