if (!localStorage.getItem("cmrOrders")) {
  localStorage.setItem("cmrOrders", JSON.stringify(orders));
}

const fetchAll = () =>
  new Promise((resolve) => {
    window.setTimeout(function () {
      resolve(JSON.parse(localStorage.getItem("cmrOrders")));
    }, 1000);
  });

const update = (id, data) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrOrders"));
    const orderIndex = items.findIndex((i) => i.id === Number(id));
   items[orderIndex] = { ...items[orderIndex], ...data };
    localStorage.setItem("cmrOrders", JSON.stringify(items));
    resolve(items);
  });

const add = (item) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrOrders"));
    const updatedOrders = [...items, item];
    localStorage.setItem("cmrOrders", JSON.stringify(updatedOrders));
    resolve(updatedOrders);
  });

const deleteItem = (id) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrOrders"));
    const updatedOrders = items.filter((i) => i.id !== Number(id));
    localStorage.setItem("cmrOrders", JSON.stringify(updatedOrders));
    resolve(updatedOrders);
  });

const find = (id) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrOrders"));
    const order = items.filter((i) => i.id === Number(id));
    resolve(...order);
  });

	// eslint-disable-next-line import/no-anonymous-default-export
export default {
	fetchAll,
	add,
	deleteItem,
	find,
	update,
};
