import React, { useState, useEffect } from "react";
import API from "../data";
import paginate from "../utils/paginate";
import Pagination from "../components/Pagination";
import BoxNp from "../components/ui/BoxNp";
import Table from "../components/table/Table";
import {
  Button,
  Dialog,
  DialogActions,
  DialogTitle,
  DialogContent,
  DialogContentText,
} from "@mui/material";
import AddOrderForm from "../components/AddOrderForm";
import Box from "../components/ui/Box";
import PreviewWindow from "../components/PreviewWindow";
import PostAddOutlinedIcon from "@mui/icons-material/PostAddOutlined";
import SearchBar from "../components/SearchBar";
import { getOrderIndex } from "../utils/orderId";
import Loader from "../components/Loader";

const OrdersPage = () => {
  const [ordersToShow, setOrdersToShow] = useState();
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;
  const [content, setContent] = useState();
  const [PreviewWindowStatus, setPreviewWindowStatus] = useState(false);
  const [findedOrders, setFindedOrders] = useState(null);
  const ordersList = findedOrders === null ? ordersToShow : findedOrders;
  const [open, setOpen] = useState(false);
	const [idToDelete, setIdToDelete] = useState();

  let columns = [
    { name: "Номер", path: "id" },
    { name: "Дата", path: "eventDate" },
    { name: "Заголовок", path: "title" },
    { name: "Цена", path: "cost" },
    { name: "Статус заказа", path: "status" },
    { name: "Удалить"},

  ];
  if (window.screen.width <= 740) {
    columns = [
      { name: "№", path: "id" },
      { name: "Дата", path: "eventDate" },
      { name: "Заголовок", path: "title" },
    ];
  }
  useEffect(() => {
    API.orders.fetchAll().then((data) => setOrdersToShow(data));
  }, []);

  const handlePageChange = (pageIndex) => {
    setCurrentPage(pageIndex);
  };
  const handleIncreasePage = (page) => {
    setCurrentPage(page + 1);
  };
  const handleDecreasePage = (page) => {
    setCurrentPage(page - 1);
  };
  const handleModalNewOrder = () => {
    setContent(
      <AddOrderForm
        onSubmit={handleOrderSubmit}
        length={getOrderIndex()}
        addClient="true"
      />
    );
    setPreviewWindowStatus(true);
  };

	const handleOpen = (id) => {
		setIdToDelete(id)
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
	const handleDelete = (id) => {
    API.orders.deleteItem(id).then((data) => setOrdersToShow(data));
		setOpen(false);
  };
  const handleOrderSubmit = (newItem, newClient) => {
    API.orders.add(newItem).then((data) => setOrdersToShow(data));
    if (newClient) {
      API.clients.add(newClient).then((data) => console.log(data));
    }
  };

  const handleSearchChange = ({ target }) => {
    const findedOrder = ordersToShow.filter((order) => {
      return (
        order.id.toString().includes(target.value) ||
        order.title.toUpperCase().includes(target.value.toUpperCase())
      );
    });
    setFindedOrders(findedOrder);
  };

  if (ordersList) {
    const ordersCrop = paginate(ordersList, currentPage, itemsPerPage);
    return (
      <div className="content-container">
        <h1>Заказы</h1>
        <SearchBar
          handleSearchChange={handleSearchChange}
          placeholder="введите номер заказа или название"
        />
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "flex-start",
            justifyContent: "center",
            width: "100%",
            flexWrap: "wrap",
          }}>
          <div
            className="slim-container"
            style={
              window.screen.width >= 724
                ? { width: "250px" }
                : { width: "100%" }
            }>
            <Box style={{ alignItems: "center" }}>
              <PostAddOutlinedIcon style={{ fontSize: "60px" }} />
              <Button variant="text" onClick={() => handleModalNewOrder()}>
                Добавить новый заказ
              </Button>
            </Box>
          </div>
          <div className="bold-container">
            <BoxNp style={{ maxWidth: "1000px", margin: "0 auto" }}>
              <Table column={columns} data={ordersCrop} endpoint="/order/" handleDelete={handleOpen}/>
              <Pagination
                styles={{
                  backgroundColor: "rgba(0, 0, 0, 0.025)",
                  borderBottom: "1px solid rgba(0, 0, 0, 0.04)",
                  padding: "20px 0",
                }}
                items={ordersToShow}
                itemsPerPage={itemsPerPage}
                currentPage={currentPage}
                onPageChange={handlePageChange}
                onPageUp={handleIncreasePage}
                onPageDown={handleDecreasePage}
              />
            </BoxNp>
          </div>
        </div>
        <PreviewWindow
          status={PreviewWindowStatus}
          setStatus={setPreviewWindowStatus}
          content={content}></PreviewWindow>
					 <Dialog
          open={open}
          keepMounted
          onClose={handleClose}
          aria-describedby="alert-dialog-slide-description">
          <DialogTitle>{"Внимание!"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              Это действие необратимо. Вы точно хотите удалить заказ #{idToDelete}?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Отмена</Button>
            <Button onClick={() => handleDelete(idToDelete)}>Удалить</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
  return <Loader />;
};

export default OrdersPage;
