import { Button } from "@mui/material";
import React, { useState, useEffect } from "react";
import API from "../../data";
import Box from "../ui/Box";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";

const OrderStatusEdit = () => {
  const [stats, setStats] = useState();
  const [newData, setNewData] = useState({ });
  const [newItemData, setNewItemData] = useState({
    name: "",
    color: "#000000",
    id: Math.floor(Math.random() * 100000000),
  });
  const [showNewInput, setShowNewInput] = useState(false);
  const [editInputId, setEditInputId] = useState();
  const toggleInput = (id, name, color) => {
    setEditInputId(id);
    setNewData({ name: name, color: color });
  };
  useEffect(() => {
    API.orderStatus.fetchAll().then((data) => setStats(data));
  }, []);

  const handleChange = ({ target }) => {
    setNewData((prevState) => ({ ...prevState, [target.name]: target.value }));
  };

  const handleDelete = (id) => {
    API.orderStatus.deleteItem(id).then((data) => setStats(data));
  };

  const handleSubmit = (id, e) => {
    e.preventDefault();
    API.orderStatus.update(id, newData).then((data) => setStats(data));
    setEditInputId();
  };

  const handleAddChange = ({ target }) => {
    setNewItemData((prevState) => ({
      ...prevState,
      [target.name]: target.value,
    }));
  };
  const handleAdd = () => {
    API.orderStatus.add(newItemData).then((data) => setStats(data));
		setNewItemData({
			name: "",
			color: "#000000",
			id: Math.floor(Math.random() * 100000000),
		})
		setShowNewInput(false)
  };
	const handleShowInput = () => {
		setShowNewInput(true)
	}
  if (stats) {
    return (
      <div style={{ width: "100%" }}>
        <h1>Настроить статусы заказа</h1>
        <div style={{ margin: "0 auto" }}>
          <Box
            style={{
              width: "600px",
              margin: "20px auto",
              justifyContent: "center",
              display: "flex",
							maxWidth: "100%"
            }}>
            {stats.map((i, index) => {
              return (
                <div
                  key={i.id}
                  style={{
                    display: "flex",
                    alignItems: "center",
                    borderBottom: "1px solid #ddd",
                    justifyContent: "space-between",
                    fontSize: "16px",
                  }}>
                  {editInputId === i.id ? (
                    <div style={{ display: "flex", alignItems: "center", flexGrow: "1" }}>
                      <input
                        type="text"
                        name="name"
                        defaultValue={stats[index].name}
                        onChange={handleChange}
                        style={{
                          borderRadius: "8px",
                          padding: "5px 15px",
                          border: "1px solid #ddd",
                          margin: "5px",
                          height: "40px",
                          width: "100%",
													fontSize: "16px"
                        }}
                      />

                      <input
                        type="color"
                        name="color"
                        defaultValue={stats[index].color}
                        onChange={handleChange}
                        style={{
                          borderRadius: "4px",
                          padding: "0px",
                          border: "0",
                          margin: "5px",
                          height: "40px",
                          backgroundColor: stats[index].color,
                        }}
                      />
                    </div>
                  ) : (
                    <div style={{ display: "flex", alignItems: "center" }}>
                      <div
                        style={{
                          borderRadius: "15px",
                          // padding: "0",
                          // border: "0",
                          // marginRight: "5px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: stats[index].color,
                          display: "flex",
                          alignItems: "center",
                        }}>{' '}</div>
                      <div
                        style={{
                          borderRadius: "8px",
                          padding: "5px 15px",
                          margin: "5px",
                          height: "40px",
                          width: "calc(100% - 40px)",
                        }}>
                        {i.name}
                      </div>
                    </div>
                  )}
                  <div style={{ display: "flex", alignItems: "center" }}>
                    {editInputId === i.id ? (
                      <div onClick={(e) => handleSubmit(i.id, e)}>
                        <CheckCircleOutlineIcon />
                      </div>
                    ) : (
                      <div onClick={() => toggleInput(i.id, i.name, i.color)}>
                        <EditOutlinedIcon />
                      </div>
                    )}
                    <div variant="outlined" onClick={() => handleDelete(i.id)}>
                      <DeleteForeverIcon />
                    </div>
                  </div>
                </div>
              );
            })}

						{showNewInput ? <div style={{ display: "flex", alignItems: "center" }}>
              <input
                type="text"
                name="name"
                value={newItemData.name}
                onChange={handleAddChange}
								style={{
									borderRadius: "8px",
									padding: "5px 15px",
									border: "1px solid #ddd",
									margin: "5px",
									height: "40px",
									width: "100%",
									fontSize: "16px"
								}}
              />
              <input
                type="color"
                name="color"
               value={newItemData.color}
                onChange={handleAddChange}
								style={{
									borderRadius: "4px",
									padding: "0px",
									border: "0",
									margin: "5px",
									height: "40px",
									backgroundColor: newItemData.color,
								}}
              />
              <div onClick={handleAdd}>
							<CheckCircleOutlineIcon />
              </div>
            </div> :
						<Button variant="outlined" onClick={handleShowInput}>Добавить статус</Button>}
          </Box>
        </div>
      </div>
    );
  }
  return "Загрузка...";
};

export default OrderStatusEdit;
