import React from "react";

const Pagination = ({
  items,
  itemsPerPage,
  currentPage,
  onPageChange,
  onPageUp,
  onPageDown,
  styles,
}) => {
  const totalPages = Math.ceil(items.length / itemsPerPage);

  return (
    <div aria-label="Page navigation example" style={styles}>
      <div className="pagination justify-content-center">
        {currentPage !== 1 && (
          <>
            <div className="page-item" onClick={() => onPageDown(2)}>
              <i className="bi bi-chevron-double-left"></i>
            </div>
            <div
              className="page-item disabled"
              onClick={() => onPageDown(currentPage)}>
              <i className="bi bi-arrow-left-short"></i>
            </div>
          </>
        )}
        <div>{currentPage}</div>
        {currentPage !== totalPages && (
          <>
            <div className="page-item" onClick={() => onPageUp(currentPage)}>
              <i className="bi bi-arrow-right-short"></i>
            </div>
            <div className="page-item" onClick={() => onPageUp(totalPages - 1)}>
              <i className="bi bi-chevron-double-right"></i>
            </div>
          </>
        )}

      </div>
			<div style={{display: "flex", justifyContent:"center", margin: "10px 0", fontSize: "12px", color: "#777" }}>Всего страниц: {totalPages} </div>
			<div style={{display: "flex", justifyContent:"center", margin: "10px 0", fontSize: "12px", color: "#777" }}>Всего записей: {items.length}</div>
    </div>
  );
};

export default Pagination;
