import React from "react";
import { useNavigate } from "react-router-dom";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
const TableBody = ({ column, data, endpoint, handleDelete }) => {
  const navigate = useNavigate();
  const handleClick = (endpoint, id) => {
    navigate(endpoint + id);
  };
  return (
    <tbody>
      {data.map((el, index) => {
        return (
          <tr
            key={"row-" + index}
            style={{
              height: "50px",
              borderBottom: "1px solid rgba(0,0,0, 0.03",
              cursor: "pointer",
            }}>
            {column.map((item, index) => {
              if (item.name === "Удалить") {
                return (
                  <td key={"col-" + index} onClick={() => handleDelete(el.id)}>
                    <DeleteForeverIcon />
                  </td>
                );
              }
              return (
                <td
                  key={"col-" + index}
                  onClick={() => handleClick(endpoint, el.id)}>
                  {el[item.path]}
                </td>
              );
            })}
          </tr>
        );
      })}
    </tbody>
  );
};

export default TableBody;
