if (!localStorage.getItem("cmrClients")) {
  localStorage.setItem("cmrClients", JSON.stringify(clients));
}

const fetchAll = () =>
  new Promise((resolve) => {
    window.setTimeout(function () {
      resolve(JSON.parse(localStorage.getItem("cmrClients")));
    }, 4000);
  });

const update = (id, data) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrClients"));
    const clientIndex = items.findIndex((i) => i.id === Number(id));
    items[clientIndex] = { ...items[clientIndex], ...data };
    localStorage.setItem("cmrClients", JSON.stringify(items));
    resolve(items);
  });

const add = (item) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrClients"));
    const newItems = [...items, item];
    localStorage.setItem("cmrClients", JSON.stringify(newItems));
    resolve(newItems);
  });

const deleteItem = (id) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrClients"));
    const updatedClients = items.filter((i) => i.id !== Number(id));
    localStorage.setItem("cmrClients", JSON.stringify(updatedClients));
    console.log(updatedClients);
    resolve(updatedClients);
  });

const find = (id) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrClients"));
    const client = items.filter((i) => i.id === Number(id));
    resolve(...client);
  });

// eslint-disable-next-line import/no-anonymous-default-export
export default {
  fetchAll,
  update,
  add,
  deleteItem,
  find,
};
