import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import API from "../data";
import {
  Button,
  Dialog,
  DialogActions,
  DialogTitle,
  DialogContent,
  DialogContentText,
} from "@mui/material";
import PageContainer from "../components/ui/PageContainer";
import Box from "../components/ui/Box";
import DeleteForeverTwoToneIcon from "@mui/icons-material/DeleteForeverTwoTone";
import EditTwoToneIcon from "@mui/icons-material/EditTwoTone";
import ArrowBackIosTwoToneIcon from "@mui/icons-material/ArrowBackIosTwoTone";
import BoxNp from "../components/ui/BoxNp";
import BoxHeader from "../components/ui/BoxHeader";
import BoxBody from "../components/ui/BoxBody";
import AddCardIcon from "@mui/icons-material/AddCard";
import AddOrderForm from "../components/AddOrderForm";
import PreviewWindow from "../components/PreviewWindow";
import ClientNotes from "../components/NotesField";
import ClientInfoContainer from "../components/ClientInfoContainer";
import Table from "../components/table/Table";
import Loader from "../components/Loader";
const ClientInfo = () => {
  const history = useNavigate();
  const params = useParams();
  const id = params.id;
  const [currentClient, setCurrentClient] = useState();
  const [orders, setOrders] = useState();
  const [open, setOpen] = useState(false);
  const [PreviewWindowStatus, setPreviewWindowStatus] = useState(false);
  const [content, setContent] = useState();
  const [newDataInput, setNewDataInput] = useState();
  let columns = [
    { name: "№", path: "id" },
    { name: "Дата", path: "date" },
    { name: "Заголовок", path: "title" },
    { name: "Цена", path: "cost" },
    { name: "Статус", path: "status" },
  ];
  if (window.screen.width <= 740) {
    columns = [
      { name: "№", path: "id" },
      { name: "Дата", path: "date" },
      { name: "Заголовок", path: "title" },
    ];
  }

  useEffect(() => {
    API.clients.find(id).then((client) => setCurrentClient(client));
    API.orders.fetchAll().then((orders) => setOrders(orders));
  }, []);
  useEffect(() => {
    if (currentClient) {
      setNewDataInput(currentClient.notes);
    }
  }, [currentClient]);
  const handleDelete = () => {
    API.clients.deleteItem(id).then(history("/clients"));
  };
  const handleChange = () => {
    history(`/client/${id}/edit`);
  };
  const handleBack = () => {
    history("/clients");
  };

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleModalNewOrder = () => {
    setContent(
      <AddOrderForm onSubmit={handleOrderSubmit} length={orders.length + 1} />
    );
    setPreviewWindowStatus(true);
  };

  const handleOrderSubmit = (newItem) => {
    API.orders.add(newItem).then((data) => setOrders(data));
  };
  const handleChangeInput = ({ target }) => {
    setNewDataInput(target.value);
  };
  const handleAddNote = () => {
    API.clients.update(id, { notes: newDataInput });
    setCurrentClient((prevState) => ({
      ...prevState,
      notes: newDataInput,
    }));
  };

  if (currentClient && orders) {
    const findedOrders = orders.filter(
      (order) => Number(order.clientId) === Number(id)
    );

    return (
      <>
        <PageContainer>
					<div className="page-header">
						<h1>Информация о клиенте</h1>
					</div>
          <div
            style={{
              display: "flex",
              flexWrap: "wrap",
              width: "100%",
              justifyContent: "center",
							marginTop: "20px",
            }}>
            <div className="slim-container">
              <Box
                style={{
                  flexDirection: "column",
                  justifyContent: "flex-start",
                }}>
                <Button
                  variant="contained"
                  onClick={handleBack}
                  startIcon={<ArrowBackIosTwoToneIcon />}
                  disableElevation>
                  Назад
                </Button>
                <Button
                  variant="outlined"
                  onClick={handleOpen}
                  startIcon={<DeleteForeverTwoToneIcon />}>
                  Удалить
                </Button>
                <Button
                  variant="outlined"
                  onClick={handleChange}
                  startIcon={<EditTwoToneIcon />}>
                  Редактировать
                </Button>
                <Button
                  variant="outlined"
                  onClick={handleModalNewOrder}
                  startIcon={<AddCardIcon />}>
                  Добавить заказ
                </Button>
              </Box>
              <ClientInfoContainer client={currentClient} />
              <ClientNotes
                handleAddNote={handleAddNote}
                handleChangeInput={handleChangeInput}
                newDataInput={newDataInput}
                notes={currentClient.notes}
              />
            </div>
            <div className="bold-container">
              <BoxNp>
                <BoxHeader>Заказы</BoxHeader>
                {findedOrders.length > 0 ? (
                  <Table
                    column={columns}
                    data={findedOrders}
                    endpoint="/order/"
                  />
                ) : (
                  <BoxBody>Заказов пока нет</BoxBody>
                )}
              </BoxNp>
            </div>
          </div>
          <PreviewWindow
            status={PreviewWindowStatus}
            setStatus={setPreviewWindowStatus}
            content={content}></PreviewWindow>
        </PageContainer>
        <Dialog
          open={open}
          keepMounted
          onClose={handleClose}
          aria-describedby="alert-dialog-slide-description">
          <DialogTitle>{"Внимание!"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              Это действие необратимо. Вы точно хотите удалить клиента?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Отмена</Button>
            <Button onClick={handleDelete}>Удалить</Button>
          </DialogActions>
        </Dialog>
      </>
    );
  }
  return <Loader />;
};

export default ClientInfo;
