import React from 'react';
const EventInfo = ({ items, id }) => {
	const event = items.find(item => item.id === id);

	if (event !== undefined) {
	return (
		<div className="event-wrapper">
			<div className="event-title"><h2>{event.title}</h2></div>
			<div className="event-info">
				<div className="event-date"><strong>Дата:</strong> {event.eventDate}</div>
				{event.time && <div className="event-time"><strong>Время:</strong> {event.time}</div>}
				{event.place && <div className="event-place"><strong>Место:</strong> {event.place}</div>}
				{event.orderNumber && <div className="order"><strong>Номер заказа:</strong></div>}
				{event.clientId && <div className="order"><strong>Номер заказа:</strong></div>}
				{event.notes && <div className="event-notes">Заметки:{event.notes}</div>}
			</div>
		</div>
	 );
	}
	return "Нет такого события"
}

export default EventInfo;
