import React from "react";
import API from "../data";
import ProjectHeader from "../components/ProjectHeader";
import ProjectItems from "../components/ProjectItems";
import ProjectTable from "../components/ProjectTable";

const ProjectsPage = () => {
	const filteredOrders = (projectId) => {
		return API.orders.filter(
      (order) =>
        order.projectType === projectId &&
        order.status !== "завершен" &&
        order.status !== "архив"
    );
	}
  return (
    <div className="content-container">
      {API.projects.map((project) => {
        return (
          <ProjectTable project={project} orders={API.orders} key={project.id}>
            <ProjectHeader project={project} />
            <ProjectItems project={project} orders={filteredOrders(project.id)} />
          </ProjectTable>
        );
      })}
    </div>
  );
};

export default ProjectsPage;
