import React, { useState } from "react";
import OrderStatusEdit from "../components/settings/OrderStatusEdit";
import ProjectsEdit from "../components/settings/ProjectsEdit";
import Profile from "../components/settings/Profile"
import "../components/settings/settings.css"

const SettingsPage = () => {
  const [pageContent, setPageContent] = useState();
  const [path, setPath] = useState();
  const togglePages = (name) => {
    if (name === "stats") {
      setPageContent(<OrderStatusEdit />);
      setPath(name);
    }
		if (name === "projects") {
      setPageContent(<ProjectsEdit />);
      setPath(name);
    }
		if (name === "events") {
      setPageContent(<OrderStatusEdit />);
      setPath(name);
    }

		if (name === "profile") {
      setPageContent(<Profile />);
      setPath(name);
    }
  };
  const welcomeText = () => {
    return (
      <div>
        <p>В этом разеделе Вы можете настроить статусы заказов</p>
      </div>
    );
  };
  return (
    <div className="content-container" style={{ backgroundColor: "#f2f4f7" }}>
      <div className="bullet-menu">
			<div
          onClick={() => togglePages("profile")}
          className={path === "profile" ? "item active" : "item"}>
          Профиль
        </div>
        <div
          onClick={() => togglePages("stats")}
          className={path === "stats" ? "item active" : "item"}>
          Статусы заказов
        </div>
        <div
          onClick={() => togglePages("projects")}
          className={path === "projects" ? "item active" : "item"}>
          Проекты
        </div>
        <div
          onClick={() => togglePages("events")}
          className={path === "events" ? "item active" : "item"}>
          Категории событий
        </div>
      </div>

      {!pageContent ? welcomeText() : pageContent}
    </div>
  );
};

export default SettingsPage;
