import React, { useState } from "react";


const ProjectStatusSelector = ({ stepStatus, index, order }) => {

  const status = ["", "Начато", "Готово", "Пауза"];
  const values = ["none", "in-progress", "done", "paused"];
  const [colorClass, setColorClass] = useState(stepStatus || "");

	const handleChangeStatus = (value) => {
		order.steps[index] = value;
		setColorClass( (prevState) => prevState = value)
console.log(order)
	}

  return (
		<div className="custom-dropdown">
    <select className={colorClass} defaultValue={stepStatus} onChange={(event) => handleChangeStatus(event.target.value) }>
      {status.map((status, index) => {
        return (
          <option key={values[index]} value={values[index]} id={index}>
            {status}
          </option>
        );
      })}
    </select>
		</div>
  );
};

export default ProjectStatusSelector;
