const number = 25;

if (!localStorage.getItem("cmrLastOrderId")) {
  localStorage.setItem("cmrLastOrderId", JSON.stringify(number));
}
export const getOrderIndex = () => {
  return JSON.parse(localStorage.getItem("cmrLastOrderId"));
};
export const setOrderIndex = (newNumber) => {
  localStorage.setItem("cmrLastOrderId", JSON.stringify(newNumber));
};
