import React, { useState, useEffect } from "react";
import ClientsList from "../components/ClientsList";
import Pagination from "../components/Pagination";
import paginate from "../utils/paginate";
import PreviewWindow from "../components/PreviewWindow";
import { Button } from "@mui/material";
import AddClientForm from "../components/AddClientForm";
import API from "../data/";
import BoxNp from "../components/ui/BoxNp";
import BoxHeader from "../components/ui/BoxHeader";
import SearchBar from "../components/SearchBar";
import Box from "../components/ui/Box";
import PersonAddAlt1Icon from "@mui/icons-material/PersonAddAlt1";
import { getUserIndex } from "../utils/userId";
import Loader from "../components/Loader";
const ClientsPage = () => {
  const [clients, setClients] = useState();
  const [PreviewWindowStatus, setPreviewWindowStatus] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;
  const [content, setContent] = useState();
  const [findedUsers, setFindedUsers] = useState(null);
  const clientsToShow = findedUsers === null ? clients : findedUsers;
  const handlePageChange = (pageIndex) => {
    setCurrentPage(pageIndex);
  };

  const handleIncreasePage = (page) => {
    setCurrentPage(page + 1);
  };
  const handleDecreasePage = (page) => {
    setCurrentPage(page - 1);
  };

  const handleModalNewClient = () => {
    setContent(
      <AddClientForm onSubmit={handleSubmit} length={getUserIndex()} />
    );
    setPreviewWindowStatus(true);
  };

  const handleSubmit = (newItem) => {
    API.clients.add(newItem).then((data) => setClients(data));
  };

  const handleSearchChange = ({ target }) => {
    const findedUser = clients.filter((client) => {
      return (
        client.name.toUpperCase().includes(target.value.toUpperCase()) ||
        client.surname.toUpperCase().includes(target.value.toUpperCase()) ||
        client.email.toUpperCase().includes(target.value.toUpperCase())
      );
    });
    setFindedUsers(findedUser);
  };

  useEffect(() => {
    API.clients.fetchAll().then((data) => setClients(data));
  }, []);

  if (clientsToShow) {
    const clientsCrop = paginate(clientsToShow, currentPage, itemsPerPage);

    return (
      <div className="content-container">
        <h1>Клиенты</h1>
        <SearchBar
          handleSearchChange={handleSearchChange}
          placeholder="введите Фамилию или email"
        />
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "flex-start",
            justifyContent: "center",
            width: "100%",
            flexWrap: "wrap",
          }}>
          <div className="slim-container">
            <Box style={{ alignItems: "center" }}>
              <PersonAddAlt1Icon style={{ fontSize: "60px" }} />
              <Button variant="text" onClick={() => handleModalNewClient()}>
                Добавить нового клиента
              </Button>
            </Box>
          </div>
          <div className="bold-container">
            <BoxNp style={{ maxWidth: "1000px", margin: "0 auto" }}>
              <BoxHeader
                style={{
                  display: "grid",
                  gridTemplateColumns: "3fr 3fr 1fr",
                  padding: "40px 80px",
                }}>
                <div className="name-content sm-header">Имя, Фамилия</div>
                <div className="sm-header">e-mail</div>
                <div className="sm-header">профессия</div>
              </BoxHeader>
              <ClientsList items={clientsCrop} />
              <Pagination
                styles={{
                  backgroundColor: "rgba(0, 0, 0, 0.025)",
                  borderBottom: "1px solid rgba(0, 0, 0, 0.04)",
                  padding: "20px 0",
                }}
                items={clientsToShow}
                itemsPerPage={itemsPerPage}
                currentPage={currentPage}
                onPageChange={handlePageChange}
                onPageUp={handleIncreasePage}
                onPageDown={handleDecreasePage}
              />
            </BoxNp>
          </div>
        </div>
        <PreviewWindow
          status={PreviewWindowStatus}
          setStatus={setPreviewWindowStatus}
          content={content}></PreviewWindow>
      </div>
    );
  }
  return <Loader />;
};

export default ClientsPage;
