import React from 'react';
import Avatar from './Avatar';
import { Link } from "react-router-dom";
import BoxBody from "./ui/BoxBody"
const Client = (props) => {

	return (
		<Link  id={props.client.id} key={props.client.id} to={"/client/"+ props.client.id}>
          <BoxBody style={{display: "grid", gridTemplateColumns: "3fr 3fr 1fr"}}>
            <div className="name-content">
              <Avatar url={props.client?.avatarUrls} size="sm"/>
              <div>
                {props.client.name} {props.client.surname}
              </div>
            </div>
            <div>{props.client.email}</div>
            <div>{props.client?.proffesion}</div>
          </BoxBody>
					</Link>
	 );
}

export default Client;
