import { Container, Button, TextField } from "@mui/material";
import React, { useState } from "react";
import todayId from "../utils/todayId";
import { getUserIndex, setUserIndex } from "../utils/userId";

const initialState = {
  id: "",
  name: "",
  surname: "",
  phone: "",
  email: "",
  proffesion: "",
  tags: [],
  type: "",
  avatarUrls: "",
};

const AddClientForm = ({ onSubmit, length }) => {
	const [index, setIndex] = useState(length)
  const [newClient, setNewClient] = useState(initialState);
  const handleChange = ({ target }) => {
    setNewClient((prevState) => ({
      ...prevState,
      avatarUrls: `https://avatars.dicebear.com/api/avataaars/${(
        Math.random() + 1
      )
        .toString(36)
        .substring(7)}.svg`,
      id: Number(todayId() + "" + index),
      [target.name]: target.value,
    }));
  };
  const handleFormSubmit = (e) => {
		setUserIndex(index + 1)
		setIndex(getUserIndex());
    e.preventDefault();
    onSubmit(newClient);
    setNewClient(initialState);
  };

  return (
    <Container maxWidth="sm">
      <form>
        <h2 style={{ marginBottom: "40px" }}>Добавить клиента</h2>
        <TextField
          value={newClient.name}
          type="text"
          name="name"
          label="Имя"
          id="name"
          margin="normal"
          size="small"
          variant="outlined"
          fullWidth
          onChange={handleChange}
        />
        <TextField
          value={newClient.surname}
          type="text"
          name="surname"
          label="Фамилия"
          id="surname"
          margin="normal"
          size="small"
          variant="outlined"
          fullWidth
          onChange={handleChange}
        />
        <TextField
          value={newClient.email}
          type="email"
          name="email"
          label="Почта"
          id="email"
          margin="normal"
          size="small"
          variant="outlined"
          fullWidth
          onChange={handleChange}
        />
        <TextField
          value={newClient.phone}
          type="tel"
          name="phone"
          label="Телефон"
          id="phone"
          margin="normal"
          size="small"
          variant="outlined"
          fullWidth
          onChange={handleChange}
        />
        <Button variant="contained" disableElevation onClick={handleFormSubmit}>
          Добавить
        </Button>
      </form>
    </Container>
  );
};

export default AddClientForm;
