import { Container, Button, TextField } from "@mui/material";
import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import API from "../data";
import { getOrderIndex, setOrderIndex } from "../utils/orderId";
import todayId from "../utils/todayId";
import { getUserIndex, setUserIndex } from "../utils/userId";

const today = new Date();
const initialState = {
  id: "",
  date: today.getDate() + "." + today.getMonth() + "." + today.getFullYear(),
  cost: 0,
  time: "",
  eventType: "",
  projectType: "",
  eventDate: "",
  place: "",
  notes: "",
  status: "новый",
  title: "",
  clientId: "",
};
const clientInitialState = {
  id: Number(todayId() + "" + Math.ceil(Math.random() * 100)),
  name: "",
  surname: "",
  phone: "",
  email: "",
  proffesion: "",
  notes: "",
  type: "",
  avatarUrls: "",
};

const AddOrderForm = ({ onSubmit, addClient }) => {
  const params = useParams();
  const id = params.id;
  const [userId, setUserId] = useState(getUserIndex());
  const [index, setIndex] = useState(getOrderIndex());
  const [newOrder, setNewOrder] = useState(initialState);
  const [clients, setClients] = useState();
  const [newClient, setNewClient] = useState(clientInitialState);

  const handleChangeClient = ({ target }) => {
    setNewClient((prevState) => ({
      ...prevState,
      avatarUrls: `https://avatars.dicebear.com/api/avataaars/${(
        Math.random() + 1
      )
        .toString(36)
        .substring(7)}.svg`,
      id: Number(todayId() + "" + userId),
      [target.name]: target.value,
    }));
  };

  useEffect(() => {
    API.clients.fetchAll().then((data) => setClients(data));
  }, []);

  const handleChange = ({ target }) => {
    if (addClient) {
      setNewOrder((prevState) => ({
        ...prevState,
        id: index,
        clientId: Number(todayId() + "" + userId),
        eventType: "work",
        [target.name]: target.value,
      }));
    } else {
      setNewOrder((prevState) => ({
        ...prevState,
        id: index,
        clientId: id,
        eventType: "work",
        [target.name]: target.value,
      }));
    }
  };
  const handleFormSubmit = (e) => {
    setOrderIndex(index + 1);
    setIndex(getOrderIndex());
    if (addClient) {
      setUserIndex(userId + 1);
      setUserId(getUserIndex());
    }
    e.preventDefault();
    onSubmit(newOrder, newClient);
    setNewOrder(initialState);
    setNewClient(clientInitialState);
  };
  if (clients) {
    return (
      <Container maxWidth="sm">
        <form>
          <h2 style={{ marginBottom: "40px" }}>Добавить заказ</h2>
          <div>Информация о проекте</div>
          <TextField
            value={newOrder.title}
            type="text"
            name="title"
            label="Заголовок"
            id="title"
            margin="normal"
            size="small"
            variant="outlined"
            fullWidth
            onChange={handleChange}
          />
          <TextField
            value={newOrder.place}
            type="text"
            name="place"
            label="Место"
            id="place"
            margin="normal"
            size="small"
            variant="outlined"
            fullWidth
            onChange={handleChange}
          />
          <TextField
            value={newOrder.eventDate}
            type="date"
            name="eventDate"
            label="Дата съемки"
            id="eventDate"
            margin="normal"
            size="small"
            variant="outlined"
            fullWidth
            onChange={handleChange}
          />
          <TextField
            value={newOrder.time}
            type="text"
            name="time"
            label="Время"
            id="time"
            margin="normal"
            size="small"
            variant="outlined"
            fullWidth
            onChange={handleChange}
          />
          <TextField
            value={newOrder.cost}
            type="text"
            name="cost"
            label="Стоимость"
            id="cost"
            margin="normal"
            size="small"
            variant="outlined"
            fullWidth
            onChange={handleChange}
          />
          {addClient && (
            <div>
              <div>Информация о клиенте</div>
              <TextField
                value={newClient.name}
                type="text"
                name="name"
                label="Имя"
                id="name"
                margin="normal"
                size="small"
                variant="outlined"
                fullWidth
                onChange={handleChangeClient}
              />
              <TextField
                value={newClient.surname}
                type="text"
                name="surname"
                label="Фамилия"
                id="surname"
                margin="normal"
                size="small"
                variant="outlined"
                fullWidth
                onChange={handleChangeClient}
              />
              <TextField
                value={newClient.email}
                type="email"
                name="email"
                label="Почта"
                id="email"
                margin="normal"
                size="small"
                variant="outlined"
                fullWidth
                onChange={handleChangeClient}
              />
              <TextField
                value={newClient.phone}
                type="tel"
                name="phone"
                label="Телефон"
                id="phone"
                margin="normal"
                size="small"
                variant="outlined"
                fullWidth
                onChange={handleChangeClient}
              />
            </div>
          )}
          <Button
            variant="contained"
            disableElevation
            onClick={handleFormSubmit}>
            Добавить
          </Button>
        </form>
      </Container>
    );
  }
  return "Загрузка...";
};

export default AddOrderForm;
