import React, { useState } from "react";
import BoxNp from "./ui/BoxNp";
import BoxBody from "./ui/BoxBody";
import BoxHeader from "./ui/BoxHeader";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import HighlightOffRoundedIcon from "@mui/icons-material/HighlightOffRounded";
import { Button } from "@mui/material";

const NotesField = ({
  handleAddNote,
  handleChangeInput,
  newDataInput,
  notes,
}) => {
  const [showNotesInput, setShowNotesInput] = useState(false);
	const toggleNotesInput = () => {
    setShowNotesInput(!showNotesInput);
  };
	const handleSubmit = (e) => {
		e.preventDefault();
		handleAddNote();
		setShowNotesInput(!showNotesInput);
	}
  return (
    <BoxNp>
      <BoxHeader
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}>
        <h2>Заметки</h2>
        <div onClick={toggleNotesInput}>
          {!showNotesInput ? <EditOutlinedIcon /> : <HighlightOffRoundedIcon />}
        </div>
      </BoxHeader>
      <BoxBody>
        <div
          style={
            showNotesInput
              ? { width: "100%", margin: "10px 0" }
              : { display: "none" }
          }>
          <textarea
            rows="10"
            type="text"
            onChange={handleChangeInput}
            value={newDataInput}
            style={{
              width: "100%",
              borderRadius: "8px",
              border: "1px solid #ddd",
              padding: "10px",
              fontSize: "14px",
            }}
          />
          <Button
            variant="outlined"
            onClick={handleSubmit}
            style={{ width: "100%" }}>
            Сохранить
          </Button>
        </div>
        <div
          style={
            !showNotesInput
              ? { width: "100%", margin: "10px 0" }
              : { display: "none" }
          }>
          {notes}
        </div>
      </BoxBody>
    </BoxNp>
  );
};

export default NotesField;
