import React from "react";
import Box from "./ui/Box";
import Avatar from "./Avatar";

const ClientInfoContainer = ({ client }) => {
  return (
    <Box>
      <div style={{ marginBottom: "20px" }}>
        <Avatar url={client.avatarUrls} size="lg" />
      </div>
      <div>
        <h1>
          {client.name} {client.surname}
        </h1>
        {client.email && <p>почта: {client.email}</p>}
        {client.phone && <p>телефон: {client.phone}</p>}
				{client.instagram && <p>instagram: @{client.instagram}</p>}
        {client.proffesion && <p>профессия: {client.proffesion}</p>}
      </div>
    </Box>
  );
};

export default ClientInfoContainer;
