const orderStatus = [
  { id: "67337-1", name: "бронь", color: "#b1dee3" },
  { id: "67337-2", name: "оплачено частично", color: "#8ad8e0" },
  { id: "67337-3", name: "оплачено полностью", color: "#57cbd9" },
  { id: "67337-4", name: "в процессе", color: "#ffc107" },
  { id: "67337-5", name: "пауза", color: "#c9e2e8" },
  { id: "67337-6", name: "завершен", color: "#e4c9a8" },
  { id: "67337-7", name: "доставляется", color: "#95dc95" },
  { id: "67337-8", name: "доставлен", color: "#95dc95" },
  { id: "67337-9", name: "архив", color: "#e4c9a8" },
  { id: "67337-10", name: "отменен", color: "#ff9b9b" },
];
if (!localStorage.getItem("cmrOrderStatus")) {
  localStorage.setItem("cmrOrderStatus", JSON.stringify(orderStatus));
}

const fetchAll = () =>
  new Promise((resolve) => {
    window.setTimeout(function () {
      resolve(JSON.parse(localStorage.getItem("cmrOrderStatus")));
    }, 100);
  });

const update = (id, data) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrOrderStatus"));
    const index = items.findIndex((i) => i.id === id);
    items[index] = { ...items[index], ...data };
    localStorage.setItem("cmrOrderStatus", JSON.stringify(items));
    resolve(items);
  });
const add = (item) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrOrderStatus"));
    const updatedItems = [...items, item];
    localStorage.setItem("cmrOrderStatus", JSON.stringify(updatedItems));
    resolve(updatedItems);
  });

const deleteItem = (id) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrOrderStatus"));
    const updatedItems = items.filter((i) => i.id !== id);
    localStorage.setItem("cmrOrderStatus", JSON.stringify(updatedItems));
    resolve(updatedItems);
  });

const replaceAll = (items) =>
  new Promise((resolve) => {
    localStorage.setItem("cmrOrderStatus", JSON.stringify(items));
    resolve(items);
  });
// eslint-disable-next-line import/no-anonymous-default-export
export default {
  fetchAll,
  update,
  add,
  deleteItem,
	replaceAll
};
