import React, { useEffect, useState } from "react";
import API from "../../data";
import Box from "../ui/Box";
import {
  Button,
  TextField,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@mui/material";
import Loader from "../Loader";
import Project from "./Project";

const ProjectsEdit = () => {
  const [projects, setProjects] = useState();
  const [open, setOpen] = useState({project: false, stage: false, step: false});
  const [newProjectName, setNewProjectName] = useState();
  const [newStage, setNewStage] = useState();
  const [newStep, setNewStep] = useState();
  const [error, setError] = useState({project: "", stage: "", step: ""});
  const [projectId, setProjectId] = useState();
  const [stageId, setStageId] = useState();

  useEffect(() => {
    API.projects.fetchAll().then((data) => setProjects(data));
  }, []);

  const handleDeleteProject = (id) => {
    API.projects.deleteItem(id).then((data) => setProjects(data));
  };

  /* Project */

  const handleOpen = () => {
    setOpen((prevState) => ({...prevState, project: true}));
  };
  const handleClose = () => {
		setOpen({project: false, stage: false, step: false});
  };
  const handleChangeProjectName = ({ target }) => {
    setNewProjectName({
      projectName: target.value.trim(),
      id: Math.floor(Math.random() * 100000000),
      stages: [],
    });
  };
  const handleAddProject = () => {
    if (newProjectName.projectName.trim() === "") {
      setError((prevState) => ({...prevState, project: "Поле не может быть пустым"}));
      return;
    }
    API.projects.add(newProjectName).then((data) => setProjects(data));
    resetAll();
  };

  /* Stage */

  const handleOpenNewStage = (Id) => {
    setOpen((prevState) => ({...prevState, stage: true}));
    setProjectId(Id);
  };
  const handleChangeNewStage = ({ target }) => {
    const newData = {
      id: Math.floor(Math.random() * 100000000),
      name: target.value,
      steps: [],
    };
    setNewStage(newData);
  };
  const handleAddStage = () => {
    if (newStage.name.trim() === "") {
			setError((prevState) => ({...prevState, stage: "Поле не может быть пустым"}));
      return;
    }
    const findedItem = projects.find((item) => item.id === projectId);
    findedItem.stages.push(newStage);
    API.projects
      .update(projectId, findedItem)
      .then((data) => setProjects(data));
    resetAll();
  };

  /* Step */

  const handleOpenNewStep = (projectId, stageId) => {
    setOpen((prevState) => ({...prevState, step: true}));
    setProjectId(projectId);
    setStageId(stageId);
  };
  const handleChangeNewStep = ({ target }) => {
    const newData = target.value;
    setNewStep(newData);
  };
  const handleAddStep = () => {
    if (newStep.trim() === "") {
			setError((prevState) => ({...prevState, step: "Поле не может быть пустым"}));
      return;
    }
    const findedItem = projects.find((item) => item.id === projectId);
    findedItem.stages.forEach((i) => {
      if (i.id === stageId) {
        i.steps.push(newStep);
      }
    });
    API.projects
      .update(projectId, findedItem)
      .then((data) => setProjects(data));
    resetAll();
  };

  const resetAll = () => {
    setOpen({project: false, stage: false, step: false});
    setError({project: "", stage: "", step: ""});
    setNewStage();
    setNewProjectName();
    setNewStep();
    setProjectId();
    setStageId();
  };

  if (projects) {
    return (
      <div style={{ width: "100%" }}>
        <h1>Проекты</h1>
        <div className="page-container">
          <Box
            style={{
              justifyContent: "center",
              display: "flex",
            }}>
            <div>
              Добавляйте свои проекты, разбивайте их на стадии и шаги.
              Отслеживайте состояние проекта на каждом этапе.
            </div>
          </Box>
          {projects && (
            <div className="projects-container">
              {projects?.map((item) => (
                <Project
                  item={item}
                  handleDeleteProject={handleDeleteProject}
                  handleOpenNewStep={handleOpenNewStep}
                  handleOpenNewStage={handleOpenNewStage}
									key={item.id}
                />
              ))}
              <Button
                variant="contained"
                style={{ width: "100%", marginBottom: "50px" }}
                onClick={handleOpen}>
                Добавить новый проект
              </Button>
            </div>
          )}
        </div>
        <Dialog open={open.project} onClose={handleClose}>
          <DialogTitle>Укажите название проекта</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="projectName"
              type="text"
              fullWidth
              variant="standard"
              name="projectName"
              onChange={handleChangeProjectName}
            />
            {error?.project && <div style={{ color: "red" }}>{error.project}</div>}
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Отмена</Button>
            <Button onClick={handleAddProject}>Добавить</Button>
          </DialogActions>
        </Dialog>

        <Dialog open={open.stage} onClose={handleClose}>
          <DialogTitle>Укажите название стадии</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="stage"
              type="text"
              fullWidth
              variant="standard"
              name="stage"
              onChange={handleChangeNewStage}
            />
            {error.stage && <div style={{ color: "red" }}>{error.stage}</div>}
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Отмена</Button>
            <Button onClick={handleAddStage}>Добавить</Button>
          </DialogActions>
        </Dialog>

        <Dialog open={open.step} onClose={handleClose}>
          <DialogTitle>Укажите название шага</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="step"
              type="text"
              fullWidth
              variant="standard"
              name="step"
              onChange={handleChangeNewStep}
            />
            {error.step && <div style={{ color: "red" }}>{error.step}</div>}
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Отмена</Button>
            <Button onClick={handleAddStep}>Добавить</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
  return <Loader />;
};

export default ProjectsEdit;
