import React from "react";
import Step from "./Step";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import { Tooltip } from "@mui/material";

const Stage = ({ stage, item, handleOpenNewStep }) => {
  return (
    <div className="stage-box">
      <div style={{ width: "100%" }}>
        <div className="stage-header">{stage.name}</div>
        {stage.steps?.map((step, index) => (
          <Step item={step} index={index} key={"step_" + index} />
        ))}
      </div>
      <div
        className="step-btn"
        onClick={() => handleOpenNewStep(item.id, stage.id)}>
        <Tooltip title="Добавить шаг" placement="top">
          <AddCircleOutlineIcon />
        </Tooltip>
      </div>
    </div>
  );
};

export default Stage;
