export const events = [
  {
    id: "e202212041",
    eventDate: "21 dec 2022",
    title: "Meeting",
    eventType: "personal",
  },
  {
    id: "e202212042",
    eventDate: "24 dec 2022",
    title: "встреча с друзьями",
    eventType: "family",
    time: "15:00",
    place: "Строгино",
  },
  {
    id: "e202212043",
    eventDate: "31 dec 2022",
    title: "Новый Год",
    eventType: "holidays",
  },
];
if (!localStorage.getItem("cmrEvents")) {
  localStorage.setItem("cmrEvents", JSON.stringify(events));
}

const fetchAll = () =>
  new Promise((resolve) => {
    window.setTimeout(function () {
      resolve(JSON.parse(localStorage.getItem("cmrEvents")));
    }, 1000);
  });

const update = (id, data) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrEvents"));
    const clientIndex = items.findIndex((i) => i.id === Number(id));
    items[clientIndex] = { ...items[clientIndex], ...data };
    localStorage.setItem("cmrEvents", JSON.stringify(items));
    resolve(items);
  });

const add = (item) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrEvents"));
    const updatedEvents = [...items, item];
    localStorage.setItem("cmrEvents", JSON.stringify(updatedEvents));
    resolve(updatedEvents);
  });

const deleteItem = (id) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrEvents"));
    const updatedEvents = items.filter((i) => i.id !== Number(id));
    localStorage.setItem("cmrEvents", JSON.stringify(updatedEvents));
    resolve(updatedEvents);
  });

const find = (id) =>
  new Promise((resolve) => {
    const items = JSON.parse(localStorage.getItem("cmrEvents"));
    const event = items.filter((i) => i.id === Number(id));
    resolve(...event);
  });

	// eslint-disable-next-line import/no-anonymous-default-export
export default {
  fetchAll,
  update,
  add,
  deleteItem,
  find,
};
