import React from "react";
import Stage from "./Stage";
import BoxHeader from "../ui/BoxHeader";
import BoxNp from "../ui/BoxNp";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import { Tooltip } from "@mui/material";

const Project = ({
  item,
  handleDeleteProject,
  handleOpenNewStep,
  handleOpenNewStage,
}) => {
  return (
    <BoxNp className="project-box">
      <BoxHeader
        style={{
          fontSize: "20px",
          fontWeight: "bold",
          display: "flex",
          justifyContent: "space-between",
        }}>
        {item.projectName}
        <div
          style={{ cursor: "pointer" }}
          onClick={() => handleDeleteProject(item.id)}>
          <Tooltip title="Удалить проект" placement="top">
            <DeleteForeverIcon />
          </Tooltip>
        </div>
      </BoxHeader>
      <div className="stages-container">
        {item.stages?.map((stage) => (
          <Stage
            stage={stage}
            item={item}
            handleOpenNewStep={handleOpenNewStep}
						key={stage.id}
          />
        ))}
        <div className="stage-btn" onClick={() => handleOpenNewStage(item.id)}>
          <Tooltip title="Добавить стадию" placement="top">
            <AddCircleOutlineIcon />
          </Tooltip>
        </div>
      </div>
    </BoxNp>
  );
};

export default Project;
